import React, { Component } from 'react'
import * as BooksAPI from './BooksAPI'

class MoveBook extends Component {

changeShelf = (value) =>{
	console.log(`MoveBook.value é ${value}`)
	if (value && value !== this.props.book.shelf){
  		BooksAPI.update(this.props.book, value).then((e) =>{
  			this.props.updateBook(this.props.book, value);
  		})
  	}
}

	render() {
		console.log(`this.props.book.shelf é ${this.props.book.shelf}`)

/*OPÇÃO MENOS OTIMIZADA: incluir dentro do option, no campo do texto: {this.props.book.shelf === 'currentlyReading' && ('CHECK')}*/

    return (
       <select value={this.props.book.shelf} onChange={event => {this.changeShelf(event.target.value)}}>
        <option value="move" disabled>Move to...</option>
        <option className={this.props.book.shelf === 'currentlyReading' && ('selected')} value="currentlyReading">Currently Reading</option>
        <option className={this.props.book.shelf === 'wantToRead' && ('selected')} value="wantToRead">{this.props.book.shelf === 'wantToRead' && ('CHECK')}Want to Read</option>
        <option className={this.props.book.shelf === 'read' && ('selected')} value="read">{this.props.book.shelf === 'read' && ('CHECK')}Read</option>
        <option className={this.props.book.shelf === 'none' && ('selected')} value="none">{this.props.book.shelf === 'none' && ('CHECK')}None</option>
      </select>
    )
  }
}

export default MoveBook


